/***VARIABLES***/
a {
  transition: all .1s ease-in-out; }
  a:hover {
    color: #A1CCC8;
    text-decoration: underline #A1CCC8; }

h5 {
  color: gray;
  font-size: 1.1rem;
  margin: 0 0 0 1rem;
